.videoCard{
    width: 270px;
    margin-bottom: 50px;
    margin-left: 15px;
    margin-right: 15px;
    border-radius: 20px;
    /* box-shadow: 0.1px -0.1px 10px rgb(164, 164, 164); */
}

.videoCard img{
    border-radius: 20px;
}
.videoCard_thumb{
    height: 140px;
    width: 100%;

}
.videoCard__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}
.videoCard__text{
    margin-left: 15px;
    margin-bottom: 10px;
}
.videoCard__text h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.videoCard__text p{
    font-size: 14px;
    color: gray;
}
.videoCard__avatar{
    height: 30px;
    width: 30px;
    margin-left: 5px;
}

.videoCard__avatar img{
    border-radius: 20px;
}

@media screen and (min-width: 518px) and (max-width: 670px) {
    .videoCard{
        width: 200px;
    }
} 
@media screen and (min-width: 900px) and (max-width: 1187px) {
    .videoCard{
        width: 370px;
    }
}

@media screen and (max-width: 300px){
    .videoCard{
        width: 200px;
        margin-left: 0;
        margin-right: 0;
    }
    /* .videoCard__text{
        display: none;
    } */
} 




















