.Login-container{
    /* border: 1px solid green; */
    width: 90%;
    height: 99vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.input-tags{
    /* border: 1px solid red; */
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 190px;
}
.username-box input{
    display: block;
    margin: auto;
    margin-bottom: 10px;
    width: 100%;
    height: 40px;
   
    border-radius: 10px;
    border: none;
    text-align: center;
    border: 2px solid black;
}
.input-tags-text{
    /* border: 1px solid green; */
    /* display: flex;
    justify-content: center; */
    text-align: center;

}
.username-box{
    width: 70%;
}
.Login-container>button{
    margin-bottom: 10px;
    width: 150px;
    height: 30px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 10px;
    border: 1px solid black;

}
.input-tags-text{
    color: rgb(0, 0, 0);
   margin: 15px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    font-weight: 700;
    color: #ff0000;
    font-size: xx-large;

}
a{
    text-decoration: none;
    color: #ff0000;
    
}
.Login-container>button:hover{

    box-shadow:0 0 2px 2px #ededed;
} 


@media screen and (min-width: 250px) and (max-width: 600px) {
    .input-tags{
     width: 100%;
     height: 35%;
    
    }
    .more-text{
     width: 90%;
    }
    .Login-container>button{
        width: 120px;
    }

   }

   @media screen and (max-width: 250px) {
    .input-tags{
     width: 100%;
     height:300px;
     /* margin-bottom: 30px; */
    }
    .more-text{
     width: 90%;
    }
    .Login-container>button{
        width: 100px;
    }

   }
   @media screen and (max-width: 605px) {
   
    .Login-container>button{
        height: 40px;
    }

   }
 
   .log-in-main-container{
     display: flex;
     
   }
 
 .sec-container{
     flex: 0.4;
 }









