.navbar{
    /* border: 1px solid rgb(0, 0, 0); */
    display: flex;
    width: 100%;
    height: 70px;
    position: sticky;
    top: 0;
    background-color: white;
}
.second{
    /* border: 1px solid rgb(255, 0, 43); */
    flex: 0.8;
    display: flex;
    justify-content: center;
    align-items: center;


}
.second input{
    height: 50%;
    width: 70%;
    border-radius: 15px;
    border: 0.5px solid black;
    padding-left: 7px;

}
.third{
    flex: 0.2;
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.user_icon{
    /* border: 1px solid green; */
    display: flex;
    flex-direction: column;
    align-items: center;
}